<template>
  <div>
    <b-button v-b-modal.modal-prevent-closing>+ Add a notification rule</b-button>
    
    <div>
      <b-table :items="rule">

      </b-table>
    </div>

    <b-modal
      id="modal-prevent-closing"
      ref="modal"
      title="Add a rule"
      @show="resetModal"
      @hidden="resetModal"
      @ok="handleOk"
    >
      <form ref="form" @submit.stop.prevent="handleSubmit">
        <label class="typo__label"><b><strong>Value</strong></b></label>
        <multiselect v-model="Value" :options="ValueState" placeholder="Select a focused value"></multiselect>
        <!-- <pre class="language-json"><code>{{ Value }}</code></pre> -->

        <!-- If -->
        <div v-if="Value === 'Rental Unit'">
          <b-input-group append="units">
            <b-form-input v-model="Amount"></b-form-input>
          </b-input-group>
        </div>
        <div v-if="Value === 'Selling unit'">
          <b-input-group append="units">
            <b-form-input v-model="Amount"></b-form-input>
          </b-input-group>
        </div>
        <div v-if="Value === 'Price to rent ratio'">
          <b-input-group append="%">
            <b-form-input v-model="Amount"></b-form-input>
          </b-input-group>
        </div>
        <div v-if="Value === ''">
          Please selected!
        </div>
        <!-- <template v-else>
          <label>Email</label>
          <input placeholder="Enter your email address" key="email-input">
        </template> -->
      
        <!-- Type Selection -->
        <label class="typo__label"><b><strong>Type</strong></b></label> 
        <multiselect v-model="Type" :options="TypeState" placeholder="Select type"></multiselect>
        <!-- <pre class="language-json"><code>{{ Type }}</code></pre> -->

        <label class="typo__label"><b><strong>District</strong></b></label>
        <!-- <multiselect v-model="District" placeholder="Which district " label="dtext" track-by="dvalue" :options="DistrictState" :multiple="true" :taggable="true" @tag="addDistrict"></multiselect> -->
        <multiselect v-model="District" :options="DistrictState" :multiple="true" :close-on-select="false" :clear-on-select="false" :preserve-search="true" placeholder="Search for districts ..."
              label="name"
              track-by="name"
              :preselect-first="true"
              :max="5"
        ></multiselect>
        <!-- <pre class="language-json"><code>{{ District }}</code></pre> -->

        <label class="typo__label"><b><strong>Period</strong></b></label> 
        <multiselect v-model="StartYear" :options="StartYearState" placeholder="Select period" label="ptext"></multiselect>
        <!-- <pre class="language-json"><code>{{ StartYear }}</code></pre> -->

        <label class="typo__label"><b><strong>Operation</strong></b></label> 
        <multiselect v-model="Operation" :options="OperationState" placeholder="Select operation" label="optext"></multiselect>
        <!-- <pre class="language-json"><code>{{ Operation }}</code></pre> -->

        <label class="typo__label"><b><strong>Condition</strong></b></label> 
        <b-input-group append="%">
          <multiselect v-model="Condition" :options="ConditionState" placeholder="Dropdown" slot="prepend"></multiselect>
          <b-form-input v-model="Con"></b-form-input>
        </b-input-group>
      </form>
    </b-modal>
  </div>
</template>

<script>
  import Multiselect from 'vue-multiselect'
  export default {
    components: {
      Multiselect
    },
    data() {
      return {
        Value:'',
        // ValueState: [{value:1, text: "Price to rent ratio"}, {value:2, text:"Rental Unit"},{value:3, text:"Selling unit"}],
        ValueState: ["Price to rent ratio","Rental Unit","Selling unit"],
        Type: '',
        // TypeState: [{value:1, text: "All"},{value:2, text: "Home"},{value:3, text: "Condo"}],
        TypeState: ["Home","Condo"],
        District: '',
        DistrictState: [
          { name: "All", dvalue: 0 },
          { name: "Bang Bon", dvalue: 50 },
          { name: "Bang Kapi", dvalue: 6 },
          { name: "Bang Khae", dvalue: 40 },
          { name: "Bang Khen", dvalue: 5 },
          { name: "Bang Kho Laem", dvalue: 31 },
          { name: "Bang Khun Thian", dvalue: 21 },
          { name: "Bang Na", dvalue: 47 },
          { name: "Bang Phlat", dvalue: 25 },
          { name: "Bang Rak", dvalue: 4 },
          { name: "Bang Sue", dvalue: 29 },
          { name: "Bangkok Noi", dvalue: 20 },
          { name: "Bangkok Yai", dvalue: 16 },
          { name: "Bueng Kum", dvalue: 27 },
          { name: "Chom Thong", dvalue: 35 },
          { name: "Din Daeng", dvalue: 26 },
          { name: "Don Mueang", dvalue: 36 },
          { name: "Dusit", dvalue: 2 },
          { name: "Huai Khwang", dvalue: 17 },
          { name: "Khan Na Yao", dvalue: 43 },
          { name: "Khlong Sam Wa", dvalue: 46 },
          { name: "Khlong San", dvalue: 18 },
          { name: "Khlong Toei", dvalue: 33 },
          { name: "Lak Si", dvalue: 41 },
          { name: "Lat Krabang", dvalue: 11 },
          { name: "Lat Phrao", dvalue: 38 },
          { name: "Min Buri", dvalue: 10 },
          { name: "Nong Chok", dvalue: 3 },
          { name: "Nong Khaem", dvalue: 23 },
          { name: "Pathum Wan", dvalue: 7 },
          { name: "Phasi Charoen", dvalue: 22 },
          { name: "Phaya Thai", dvalue: 14 },
          { name: "Phra Khanong", dvalue: 9 },
          { name: "Phra Nakhon", dvalue: 1 },
          { name: "Pom Prap Sattru Phai", dvalue: 8 },
          { name: "Prawet", dvalue: 32 },
          { name: "Rat Burana", dvalue: 24 },
          { name: "Ratchathewi", dvalue: 37 },
          { name: "Sai Mai", dvalue: 42 },
          { name: "Samphanthawong", dvalue: 13 },
          { name: "Saphan Sung", dvalue: 44 },
          { name: "Sathon", dvalue: 28 },
          { name: "Suan Luang", dvalue: 34 },
          { name: "Taling Chan", dvalue: 19 },
          { name: "Thawi Watthana", dvalue: 48 },
          { name: "Thon Buri", dvalue: 15 },
          { name: "Thung Khru", dvalue: 49 },
          { name: "Wang Thonglang", dvalue: 45 },
          { name: "Watthana", dvalue: 39 },
          { name: "Yan Nawa", dvalue: 12 }
        ],
        StartYear: '',
        StartYearState: [{pvalue:1, ptext:"1 month"},{pvalue:3, ptext:"3 month"},{pvalue:6, ptext:"6 month"},{pvalue:12, ptext:"1 yaer"}],
        Operation:'',
        OperationState:[{opvalue:">", optext:"increase"},{opvalue:"<", optext:"decrease"},{opvalue:"=", optext:"equal"}],
        PC:'',
        Amount:'',
        Con:'',
        Condition:'',
        ConditionState: ["less than","more than","equal"],
        submittedValue: [],
        submittedType: [],
        submittedDistrict: [],
        submittedStartYear: [],
        submittedEndYear: [],
        submittedOperation: [],
        submittedNum: [],
        rule: [],
        //fields: ['Value','Type', 'District', 'Start Year', 'End Year','With', 'Amount', 'Edit']
      }
    },
    methods: {
      resetModal() {
        Value = ''
        ValueState = null
        Type = ''
        TypeState = null
        District = ''
        this.DistrictState = null
        this.StartYear = ''
        this.StartYearState = null
        this.Operation = ''
        this.OperationState = null
        this.PC = ''
        this.PCState = null
      },
      checkFormValidity() {
        const valid = this.$refs.form.checkValidity()
        this.TypeState = valid ? 'valid' : 'invalid'
        return valid
      },
      handleOk(bvModalEvt) {
        // Prevent modal from closing
        bvModalEvt.preventDefault()
        // Trigger submit handler
        this.handleSubmit()
      },
      handleSubmit() {
        // Exit when the form isn't valid
        if (!this.checkFormValidity()) {
          return "Fail"
        }

        // Hide the modal manually
        this.$nextTick(() => {
          this.rule.push({value:this.Value, type: this.Type, district: this.District.name, period: this.StartYear.ptext,
          with: this.Operation.opvalue, amount: this.Amount , condition: this.Condition, equalTo: this.Con ,Edit:''})
          this.$refs.modal.hide()
        })
      }
    }
  }
</script>